import React, { useState, useEffect, useRef } from 'react';
// import axios from 'axios';
// import Cookies from 'universal-cookie';

// const cookies = new Cookies();

function Test() {
  const [issues, setIssues] = useState([]);
  const [page, setPage] = useState(1);

  const getUser = () => {
    let fakeData = {
      1: [1, 2, 3, 4, 5],
      2: [6, 7, 8, 9, 10],
      3: [11, 12, 13, 14, 15],
      4: [16, 17, 18, 19, 20],
      5: [21, 22, 23, 24, 25],
    };
    console.log('fakeData', fakeData[page]);
    setIssues([...issues, ...fakeData[page]]);
  };

  useEffect(() => {
    getUser(page);
  }, [page]);

  console.log(issues);

  const loadingRef = useRef(null);
  useEffect(() => {
    const options = {
      root: null,
      rootMargin: '0px',
      threshold: 1,
    };

    const callback = (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          console.log('aaaaaa'); //if成立 在可視範圍中
          setPage(page + 1);
        }
      });
    };

    const observer = new IntersectionObserver(callback, options);

    if (loadingRef.current !== null) {
      observer.observe(loadingRef.current);
    }

    return () => {
      observer.disconnect(loadingRef.current);
    };
  }, [loadingRef, page]);

  return (
    <>
      <div style={{ border: 'solid 10px black', padding: '100px' }}>12345</div>
      <div style={{ border: 'solid 10px black', padding: '100px' }}>12345</div>
      <div style={{ border: 'solid 10px black', padding: '100px' }}>12345</div>
      <div style={{ border: 'solid 10px black', padding: '100px' }}>12345</div>
      <div style={{ border: 'solid 10px black', padding: '100px' }}>12345</div>
      <div ref={loadingRef}>Loading...</div>
    </>
  );
}

export default Test;
